.card {
  display: flex;
  flex-flow: column wrap;
  margin: 5px;
  padding: 10px;
  width: 200px;
  line-height: 50px;
  border-radius: 20px;
  box-shadow: 2px 2px 3px rgb(38, 39, 33, .5);
}

.name {
  margin-right: auto;
  margin-left: auto;
  color: whitesmoke;
}

.programming-lenguage-img {
  height: 90px;
}

